
          @import "@/styles/global.sass"
        






































































































































































































































































































































































































































































































































.code
  height: 100%
  opacity: 0
  pointer-events: none
  position: relative
  outline: none
  transition: opacity .5s ease-in
  transition-delay: .7s


  &.ready
    opacity: 1

  // #0 ::v-deep
  //   .underScore
  //     display: inline-block
  //     font-size: 0.9em
  //     transform: translateY(4px) !important
  //     filter: saturate(70%)
  //   .underScoreHidden
  //     opacity: 0
  //   .CodeMirror-linenumber
  //     opacity: 0


.codemirror ::v-deep
  .CodeMirror

  .CodeMirror-gutters
    border: none
  .CodeMirror-cursor
    border-left: 1px solid #FFCC00
    opacity: 1
    border-right: none

  .CodeMirror-selected
    background: transparent

  .CodeMirror-linenumber
    font-size: 20px
    line-height: 22px
    font-weight: normal

  .CodeMirror-line
    // line-break: anywhere !important

    span > span
      transition: filter 1s ease-out
      filter: grayscale(40%) brightness(80%)
      font-size: 20px
      line-height: 22px
      font-weight: normal

    .correct
      filter: none

    .next-char
      border-bottom: 2px solid white

    .mistake
      background-color: rgba(255, 255, 255, .3)


  .CodeMirror-scroll
    padding-right: .7em

  .CodeMirror-vscrollbar
    &::-webkit-scrollbar
      width: $gap / 2 !important
    &::-webkit-scrollbar-thumb
      background: linear-gradient(to top, $purple-gradient-colors) !important
    &::-webkit-scrollbar-track
      background-color: $grid-color !important



.completed
  .codemirror ::v-deep

    .mistake
      background-color: transparent
      transition: margin $nav-trans-dur $nav-trans-timing, padding $nav-trans-dur $nav-trans-timing, background-color .5s ease-out 1s, box-shadow .5s ease-out 1s



.heatMap
  pointer-events: auto

  .codemirror
    ::v-deep
      .correctedMistake
        background-color: $grid-color
        box-shadow: -2px 2px 8px 0px rgba(0,0,0,.6)
        // outline: 0.2em solid $grid-color
        padding: 0.1em 0.2em
        // margin: 0.2em
        opacity: 1


@keyframes opacity-enter
  from
    opacity: 0
  to
    opacity: 1
@keyframes opacity-leave
  from
    opacity: 1
  to
    opacity: 0

.pop-up
  position: fixed
  top: 0
  bottom: 0
  left: 0
  right: 0
  background-color: rgba($grid-color, .5)
  display: flex
  justify-content: space-around
  align-items: center
  pointer-events: all
  user-select: none
  z-index: 10
  font-size: 4rem
  opacity: 1
  animation: opacity-enter .7s ease-out forwards
  cursor: default

  &.hidden
    pointer-events: none
    animation: opacity-leave .7s linear forwards .1s

  &.clickable
    // pointer-events: all
    h2
      cursor: pointer
  &.small-font
    font-size: 1.5rem

    h2
      max-width: 50vw
      text-align: center

