
          @import "@/styles/global.sass"
        
































































































































main
  height: calc(100vh - #{2 * $gap})
  max-width: 1300px
  width: 100%
  position: relative
  flex-direction: column
  display: flex
  justify-content: flex-start

  .codeEditor
    flex-grow: 1

.top-bar
  display: flex
  justify-content: space-between
  align-items: center
  position: relative
  animation: opacity-enter .5s ease-out forwards .7s
  animation-fill-mode: both
  margin-bottom: 1rem

  .info
    position: relative
    display: flex
    align-items: center
    min-width: 0

    .languageName
      font-size: 2rem
      margin-right: 1em
    .codeInfo
      display: flex
      justify-content: space-between
      flex-direction: column
      overflow: hidden
      p
        margin-bottom: $grid-gap

  .buttons
    flex-shrink: 0
    button
      text-align: center
      width: 150px
      height: 47px
      background: $grid-color
      margin-left: max(10px, calc(20vw - 210px))
      cursor: pointer

@keyframes opacity-enter
  from
    opacity: 0
  to
    opacity: 1


